.btn {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.img {
  border-radius: 50%;
  margin: 0 10px auto;
}
.btn-sign-out{
  display: block;
  margin-bottom: 10px;
  margin-left: auto;
  position: absolute;
  top: 0;
  right: 0;
}